* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    background-color: rgb(184, 245, 221);
    width: 100%;
}

/////////////////APP///////////////

.App {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

/////////////////APP///////////////

////////////////HEADER////////////////

.header {
    // border: 1px solid blue;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 0;
    width: 20%;
}
#reactLogo {
    width: 100%;
    min-width: 300px;
}

////////////////HEADER////////////////

/////////////MAIN/////////////////

.main {
    // border: 1px solid red;
    width: 40%;
    min-width: 350px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    background-color: #f48b29;
    border-radius: 30px;
    h1 {
        display: block;
        width: 100%;
        text-align: center;
        color: white;
        margin: 30px 0;
        font-size: 40px;
    }
}
.cards {
    // border: 1px solid green;
    width: 250px;
    height: 180px;
    margin: 20px 10px;
    background-color: #fbecdc;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 20px;
    position: relative;
    overflow: auto;
    transition: 0.3s;
    cursor: pointer;
    h2 {
        font-family: "Courier New", Courier, monospace;
        padding-bottom: 30px;
        // width: 100%;
    }
    .cardsLogoContainer {
        padding: 20px;
        .cardsLogoImg {
            height: 70px;
            width: auto;
            transition: 0.3s;
            // transform-origin: 0 0;
        }
    }
    .info {
        display: flex;
        justify-content: center;
        align-items: center;
        // background-color: green;
        width: 100%;
        height: 100%;
    }
}

.active {
    color: blue;
}

.cards:hover .cardsLogoImg {
    // transition: 1s;
    transform: scale(1.3);
}

.cards:hover {
    box-shadow: 0px 0px 10px 3px #001a79;
}

/////////////MAIN/////////////////
