* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  background-color: #b8f5dd;
  width: 100%;
}

.App {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 20px 0;
  width: 20%;
}

#reactLogo {
  width: 100%;
  min-width: 300px;
}

.main {
  width: 40%;
  min-width: 350px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
  background-color: #f48b29;
  border-radius: 30px;
}

.main h1 {
  display: block;
  width: 100%;
  text-align: center;
  color: white;
  margin: 30px 0;
  font-size: 40px;
}

.cards {
  width: 250px;
  height: 180px;
  margin: 20px 10px;
  background-color: #fbecdc;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-radius: 20px;
  position: relative;
  overflow: auto;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  cursor: pointer;
}

.cards h2 {
  font-family: "Courier New", Courier, monospace;
  padding-bottom: 30px;
}

.cards .cardsLogoContainer {
  padding: 20px;
}

.cards .cardsLogoContainer .cardsLogoImg {
  height: 70px;
  width: auto;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.cards .info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
  height: 100%;
}

.active {
  color: blue;
}

.cards:hover .cardsLogoImg {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}

.cards:hover {
  -webkit-box-shadow: 0px 0px 10px 3px #001a79;
          box-shadow: 0px 0px 10px 3px #001a79;
}
